
body {
  margin: 0;
}
div {
  text-align: center;
}
.controls {
  padding: 10px 0;
  font-size: 1.4em;
}
.pot {
  width: 100px;
}
.result {
  font-size: 3em;
  white-space: nowrap;
}
.icon-holder {
  text-align: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
h2 {
  text-align: center;
}




